<template>
  <base-button 
    v-if="isPlaying" 
    @click="togglePlay"
    class="btn play-pause"><pause-icon></pause-icon> 
  </base-button> 
  <base-button 
    v-else 
    @click="togglePlay"
    class="btn play-pause"><play-icon></play-icon> 
  </base-button> 
  <base-card class="card">
    <input 
      @input="receiveInput"
      v-model="val"
      type="text" 
      maxlength="3"
      placeholder="Nr" 
    /> 
    <base-button 
      class="btn" 
      @click="decrementNumber"><arrow-left></arrow-left> 
    </base-button> 
    <base-button 
      class="btn" 
      @click="incrementNumber"><arrow-right></arrow-right> 
    </base-button> 
    <base-button 
      class="btn" 
      @click="nextInGroup"><arrow-down></arrow-down> 
    </base-button> 
    <base-button 
      class="btn" 
      @click="prevInGroup"><arrow-up></arrow-up> 
    </base-button> 
    <base-button 
      class="btn" 
      @click="turnPage"><more-icon></more-icon> 
    </base-button> 
  </base-card> 
</template>

<script>
import { ref } from 'vue';

import MoreIcon from '../icons/MoreIcon.vue';
import PlayIcon from '../icons/PlayIcon.vue';
import PauseIcon from '../icons/PauseIcon.vue';
import ArrowUp from '../icons/ArrowUp.vue';
import ArrowDown from '../icons/ArrowDown.vue';
import ArrowRight from '../icons/ArrowRight.vue';
import ArrowLeft from '../icons/ArrowLeft.vue';

export default {
  components: {
    PlayIcon,
    PauseIcon,
    MoreIcon,
    ArrowUp,
    ArrowDown,
    ArrowRight,
    ArrowLeft,
  },
  emits: [
    'turn-page',
    'set-number',
    'increment-number',
    'decrement-number',
    'next-in-group',
    'prev-in-group',
  ],
  data() {
    return {
      val: '',
      oldVal: ''
    };
  },
  watch: {
    val(newVal) {
      if (!(newVal < 120 && newVal > 0)) {
        this.val = this.oldval;
      } else {
        this.oldVal = newVal;
      }
    }
  },
  setup(_, context) {
    window.addEventListener('keydown', e => {
      if (e.keyCode == 32) { togglePlay() }
    });
    const turnPage = () => context.emit('turn-page');

    function receiveInput() {
      const n = parseInt(event.target.value);
      if (n < 120 && n > 0) {
        context.emit('set-number', n);
      }
    }

    const isPlaying = ref(false);
    let interval;
    const togglePlay = () => {
      if (!isPlaying.value) {
        interval = setInterval(() => {
          context.emit('increment-number');
        }, 2000);
      }
      else if (isPlaying.value) {
        clearInterval(interval);
      } 
      isPlaying.value = !isPlaying.value;
    };

    const incrementNumber = () => { context.emit('increment-number') };
    const decrementNumber = () => { context.emit('decrement-number') };
    const nextInGroup = () => { context.emit('next-in-group') };
    const prevInGroup = () => { context.emit('prev-in-group') };

    return {
      isPlaying,
      togglePlay,
      receiveInput,
      turnPage,
      incrementNumber,
      decrementNumber,
      nextInGroup,
      prevInGroup,
    };
  },
};
</script>

